.container{display:grid;grid-template-columns:200px 400px;grid-template-rows:auto;grid-column-gap:1rem;padding:1rem;max-width:20rem}.container>img{align-self:start;width:100%}.container>div{vertical-align:top}.container h2{font-weight:500;margin:0 0 0.5rem 0}.container a{position:relative;bottom:0;text-decoration:none;color:#000000}.container a:hover{color:#000000;font-weight:bolder;text-decoration:underline}
