.login{background-image:linear-gradient(#eaeaea, #ecf4f3);display:flex;align-items:center;flex-direction:column;justify-content:center;padding:0px 30px;min-height:calc(100vh - 200px)}.login__container{color:#335166;display:flex;flex-direction:column;justify-content:space-around;min-height:600px;padding:60px 68px 40px;width:450px}.login__container--form{display:flex;flex-direction:column}.login__container--form label{font-size:14px}.login__container--form>input{background-color:rgba(255,255,255,0.1);border:2px solid #335166;border-radius:25px;color:#335166;font-family:'Muli', sans-serif;font-size:16px;height:50px;margin-bottom:20px;outline:none;padding:0 20px}.login__container--remember-me{color:#335166;display:flex;justify-content:space-between;margin-top:10px;font-weight:500}.login__container--remember-me a{color:#335166;font-size:14px;font-weight:500;text-decoration:none}.login__container--remember-me a:hover{text-decoration:underline}.login__container--social-media>div{align-items:center;display:flex;font-size:14px;margin-bottom:10px;font-weight:700}.login__container--social-media>div>img{margin-right:10px;width:30px}.login__container--register{font-size:14px}.login__container--register a{color:#335166;font-weight:bold;font-size:16px;text-decoration:none}.login__container--register a:hover{text-decoration:underline}.login__container--button{background-color:#335166;border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0}
